import { gsap } from 'vendors/gsap';

export default class OpacityAnimation implements AnimationInterface {
  el?:HTMLElement;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {
      gsap.set(this.el, {alpha: 0, y: 20});

      this.tl = gsap.timeline(
        { 
          paused: true,
          autoRemoveChildren: false,
          repeatRefresh: true,
        }
      )
        .fromTo(this.el, 
          {
            alpha: 0,
            y: 20,
          },
          { 
            alpha: 1, 
            y: 0, 
            delay: 0.5,
            duration: 0.6,
            ease: 'power2.out'
          }
      );
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0)
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }

  update = () => {
    //
  }

  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse()
    }
  }

  dispose() {
    this.tl?.kill();
    this.tl = undefined;
  }
}
